
































































import footfoot from '../../components/footer.vue'
export default {
	components: {
		footfoot,
	},
	data() {
		return {
			hasLogin: '',
			userData: null
		};
	},
	onLoad() {
		var _this = this
		uni.getStorage({
			key: 'userData',
			success: function (res) {
				// console.log(JSON.stringify(res.data));
				_this.userData = res.data
			}
		});
		uni.getStorage({
			key: 'hasLogin',
			success: function (res) {
				// console.log(JSON.stringify(res.data));
				_this.hasLogin = res.data
			}
		});
	},
	methods: {
		goBianji() {
			uni.navigateTo({
				url: './bianji/bianji',
			});
		},
		goDongtai() {
			uni.navigateTo({
				url: './dongtai/dongtai',
			});
		},
		goWodeguanzhu() {
			uni.navigateTo({
				url: './wodeguanzhu/wodeguanzhu',
			});
		},
		goWodefensi() {
			uni.navigateTo({
				url: './wodefensi/wodefensi',
			});
		},
		goLishi() {
			uni.navigateTo({
				url: './lishi/lishi',
			});
		},
		goShoucang() {
			uni.navigateTo({
				url: './shoucang/shoucang',
			});
		},
		goShezhi() {
			uni.navigateTo({
				url: './shezhi/shezhi',
			});
		},
		goFankui() {
			uni.navigateTo({
				url: './fankui/fankui',
			});
		},
		loginOut() {
			uni.setStorage({
				key: 'hasLogin',
				data: false,
				success: function () {
					console.log('success');
				}
			});
			uni.removeStorage({
				key: 'userData',
				success: function (res) {
					console.log('success');
					uni.redirectTo({
						url: '../login/login-wechat/login-wechat'
					});
				}
			});
		},
		goLogin() {
			uni.redirectTo({
				url: '../login/login-wechat/login-wechat'
			});
		}
	}
}



